const ProjectsGallery = () => {
    const [selectedProject, setSelectedProject] = useState(null);

    const handleOpen = (project) => {
        setSelectedProject(project);
    };

    const handleClose = () => {
        setSelectedProject(null);
    };

    return (
        <Box sx={{ width: '100%', maxWidth: '1100px', margin: 'auto', padding: 4 }}>
            <Grid container spacing={4} justifyContent="center">
                {projects.map((project) => (
                    <Grid item xs={12} sm={6} md={4} key={project.id}>
                        <Card
                            sx={{ cursor: 'pointer', boxShadow: 3, borderRadius: 2 }}
                            onClick={() => handleOpen(project)}
                        >
                            <CardMedia component="img" image={project.image} alt={project.title}   
                            sx={{
                                height: 200,
                                width: '420px',
                                objectFit: 'cover'
                                }}/>
                            <CardContent>
                                <Typography variant="h6" fontWeight="bold">{project.title}</Typography>
                                <Typography variant="body2" color="text.secondary" mt={1}>
                                    {project.description}
                                </Typography>
                                <Box mt={1}>
                                    {project.tags.map((tag, index) => (
                                        <Typography 
                                            key={index} 
                                            variant="body2" 
                                            sx={{ color: 'primary.main', display: 'inline', mr: 1 }}
                                        >
                                            {tag}
                                        </Typography>
                                    ))}
                                </Box>
                            </CardContent>
                        </Card>
                    </Grid>
                ))}
            </Grid>

            {/*  ( ) */}
            <Modal open={!!selectedProject} onClose={handleClose} sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                <Box sx={{ 
                    width: 800, 
                    bgcolor: 'white', 
                    boxShadow: 24, 
                    p: 3, 
                    borderRadius: 2,
                    position: 'relative'
                }}>
                    <IconButton 
                        onClick={handleClose} 
                        sx={{ position: 'absolute', top: 8, right: 8 }}
                    >
                        <CloseIcon />
                    </IconButton>
                    {selectedProject && (
                        <>
                            <Typography variant="h5" fontWeight="bold" mt={2}>{selectedProject.title}</Typography>
                            <Typography variant="body1" color="text.secondary" mt={1} sx={{ whiteSpace: 'pre-line' }}>
                                {selectedProject.longDescription}
                            </Typography>
                            <Box mt={2}>
                                {selectedProject.tags.map((tag, index) => (
                                    <Typography 
                                        key={index} 
                                        variant="body2" 
                                        sx={{ color: 'primary.main', display: 'inline', mr: 1 }}
                                    >
                                        {tag}
                                    </Typography>
                                ))}
                            </Box>
                        </>
                    )}
                </Box>
            </Modal>
        </Box>
    );
};

export default ProjectsGallery;
